* {
    padding: 0;
    margin: 0;
    border: 0;
    background: transparent;
    color: inherit;
    line-height: 160%;
    font-style: normal;
    box-sizing: border-box;
    word-wrap: break-word;
    word-break: keep-all;
    touch-action: auto;
    -webkit-tap-highlight-color: transparent !important;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
}

*:before,
*:after {
    box-sizing: inherit;
}

html {
